<template>
	<view>
		<view class="top_hr"></view>
		<view style="position: fixed;top: 0;width: 100%;">
			<headers title="消息" :leftIcon="true" />
		</view>
		
		
		<view class="message">
			<view v-if="list.length===0">
				<text>暂无更多消息</text>
			</view>
			<view class="item" v-for="(item,index) in list" :key="index">
				<text>{{item.content}}</text>
			</view>
		</view>
	</view>
</template>

<script>
	import headers from '@/components/header.vue';
	import popup from '@/components/uni-popup/uni-popup.vue'
	export default {
		components:{headers,popup},
		data() {
			return {
				token:'',
				list:[]
			};
		},
		mounted() {
			let {token} = uni.getStorageSync('userinfo');
			this.token = token;
			this.getMsg();
		},
		methods:{
			async getMsg(){
				let obj = {token:this.token,offset:1,limit:10};
				let res = await this.$Post('/api/Index/IndexNotice',obj);
				if(res.data.code===1){
					this.list = res.data.data.list;
				}
				// console.log(res)
			}
		}
	}
</script>

<style lang="scss" scoped>
	.message{
		width: 100%;
		box-sizing: border-box;
		padding: 40rpx 20rpx;
		.item{
			width: 100%;
			border-bottom: 1px solid #f0f0f0;
			box-sizing: border-box;
			padding: 15rpx 0;
			margin-bottom: 15rpx;
		}
	}
</style>
